import React, { Component } from "react";
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

class TabsTwo extends Component{
    render(){
        let 
        tab3 = "Experiência",
        tab4 = "Educação";
        const { tabStyle } = this.props
        return(
            <div>
                {/* Start Tabs Area */}
                <div className="tabs-area">
                    <div className="container">
                        <div className="row">
                            <div className="col-lg-12">
                                <Tabs>
                                    <TabList  className={`${tabStyle}`}>
                                        <Tab>{tab3}</Tab>
                                        <Tab>{tab4}</Tab>
                                    </TabList>

                                    <TabPanel>
                                       <div className="single-tab-content">
                                           <ul>
                                               <li>
                                                   <a href="#about">Desenvolvedor Senior/Tech-Lead<span> - Lanlink</span></a> 2019 - Atual
                                               </li>
                                               <li>
                                               <a  href="#about">Desenvolvedor Pleno<span> - Lanlink</span></a> 2016 - 2019
                                               </li>
                                               <li>
                                               <a  href="#about">Desenvolvedor ERP<span> - Fluxus Consultoria</span></a> 2011 - 2016
                                               </li>
                                           </ul>
                                       </div>
                                    </TabPanel>

                                    <TabPanel>
                                       <div className="single-tab-content">
                                           <ul>
                                               <li>
                                               <a  href="#about"> Analise e desenvolvimento de sistemas<span> - Faculdade Lourenço Filho</span></a> 2012-2015
                                               </li>
                                               <li>
                                               <a  href="#about">  Pós graduação em Java <span> Estácio FIC</span></a> 2015 - Incompleta
                                               </li>
                                           </ul>
                                       </div>
                                    </TabPanel>
                                    
                                </Tabs>
                            </div>
                        </div>
                    </div>
                </div>
                {/* End Tabs Area */}
            </div>
        )
    }
}



export default TabsTwo;