import React ,{ Component }from "react";

const skills = [
    {
        icon: <img src="/assets/images/logo/node.svg" height="120" alt="NodeJS" />,
    },
    {
        icon: <img src="/assets/images/logo/react.svg" height="120" alt="React" />,
    },
    {
        icon: <img src="/assets/images/logo/angular.svg" height="120" alt="Angular" />,
    },
    { 
        icon: <img src="/assets/images/logo/docker.svg" height="120" alt="Docker" />,
    },
    {
        icon: <img src="/assets/images/logo/firebase.png" height="120" alt="SQL" />,
    },
    { 
        icon: <img src="/assets/images/logo/azure.png" height="120" alt="Azure" />,
    }
]


class SkillList extends Component{
    render(){
        const {column } = this.props;
        const SkillContent = skills.slice(0 , this.props.item);
        
        return(
            <React.Fragment>
                <div className="row">
                    {SkillContent.map( (val , i) => (
                        <div className={`${column}`} key={i}>
                                <div className="service service__style--2" style={{display:'flex', alignItems:'center', flexDirection:'column'}}>
                                    <div className="icon">
                                        {val.icon}
                                    </div>
                                 
                                </div>                       
                        </div>
                    ))}
                </div>
            </React.Fragment>
        )
    }
}
export default SkillList;
